	module.exports = {"data":{"markdownRemark":{"html":"<p>中学時代の文化祭※で、歴史物の掲示をすることとなったため、ネタ出しに悩んでいたところ、「あ、大化の改新とタイムマシンって、言葉の響きが似てる」 ってことに気づき、即興で描いてみた漫画です。\n※ちょっと記憶があやふや。もしかしたら夏休みの宿題だったかも……？</p>\n<p>当時ピグマ信者だったので、漫画描くときは猿のようにピグマ使ってました。\nしまいにゃ授業中の番所にまでピグマを使いだす始末。この画風は当時二年くらいはまり続けてました。</p>\n<p>ところで、嫁が二次元から出てこないんですが、早く出てきてもらえないと八代目どころか一代目で終わりそうなんですが（ｒｙ</p>","frontmatter":{"cover":null,"date":"1994-10-30T18:00:34+09:00","strDate":"1994/10/30","title":"まんが 大化の改新"}}},"pathContext":{"path":"/ja/blog/1994/10/30-taika-reform/","langKey":"ja"}}

